import React from "react";
import { getSessionsPrintWorks } from "../Services/sessions";
import MaterialTable from "material-table";
import Cookie from 'js-cookie';
import _ from 'lodash';
import { Container } from 'react-bootstrap';
import NavBar from '../Components/NavBar';

export default class SessionsPrintWorks extends React.Component {
  constructor(props) {
    super(props);
    document.title = "Le Consulat - Services d'impressions factures";
    this.state = {
      info: [],
      toDisplay: [],
      isLoading: true,
      error: false,
      waiter: "all",
    };
    this.columns = [
      {
        title: "Serveur",
        field: "nameOfServer",
      },
      {
        title: "Client",
        field: "nameOfClient",
      },
      {
        title: "URL",
        field: "link",
      },
    ];
  }

  componentDidMount() {
    getSessionsPrintWorks(Cookie.get('token'))
      .then(data => {
        if (data.find) {
          let data2Put = [];
          for (let index in data.sessions) {
            data2Put.push({ nameOfClient: data.sessions[index].nameOfClient, link: <a target="_blank" href={data.sessions[index].invoiceUrl}>Voir</a>,...data.sessions[index] })
          }
          this.setState({ info: data2Put, toDisplay: data2Put, isLoading: false, error: false });
        } else {
          this.setState({ error: true });
        }
      })
      .catch(() => {
        this.setState({ error: true });
      });
  }

  _handleWaiter = (event) => {
    this.setState({ waiter: event.target.value }, () => {
      this.state.waiter = event.target.value;
      if (this.state.waiter === "all") {
        this.setState({ toDisplay: this.state.info });
      } else {
        const until = _.filter(this.state.info, (o) => (o.nameOfServer === this.state.waiter));
        this.setState({ toDisplay: until });
      }
    });
  }

  render() {
    if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Services impressions bons</h1>
            <em>Une erreur a eu lieu, ressayez plus tard ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Services d'impressions factures</h1>
            Serveur:{' '}
            <select onChange={this._handleWaiter}>
              <option value="all" selected>Tout</option>
              <option value='FANNY'>FANNY</option>
              <option value='CHRISTELLE'>CHRISTELLE</option>
              <option value='NDAYA CHRISTELLE'>NDAYA CHRISTELLE</option>
              <option value='EXAUCE'>EXAUCE</option>
              <option value='SANDRA'>SANDRA</option>
              <option value='PATIENT'>PATIENT</option>
              <option value='GRACIA'>GRACIA</option>
            </select>
            <br />
            <MaterialTable
              title="Factures"
              data={this.state.toDisplay}
              columns={this.columns}
              options={{
                search: true,
                paging: false,
                filtering: true,
                exportButton: true,
              }}
              isLoading={this.state.isLoading}
            />
          </Container>
        </>
      );
    }
  }
}
