import 'bootstrap/dist/css/bootstrap.min.css';
import '@fortawesome/fontawesome-free/css/all.min.css';
import "./CSS/blink.css";

import React from "react";

import Login from './Pages/Login';
import Home from './Pages/Home';
import Users from './Pages/Users';
import Clients from './Pages/Clients';
import Products from './Pages/Products';
import Stocks from './Pages/Stocks';
import TransactionDetail from './Pages/TransactionDetail';
import TransactionWholeSale from './Pages/TransactionWholeSale';
import TransactionManager from './Pages/TransactionManager';
import GetOneSession from './Pages/GetOneSession';
import SessionsInProgress from './Pages/SessionsInProgress';
import Debt from './Pages/Debt';
import SessionsReport from './Pages/SessionsReport';
import SessionsReportPeriod from './Pages/SessionsReportPeriod';
import GuichetTransactions from './Pages/GuichetTransactions';
import GuichetCategories from './Pages/GuichetCategories';
import NotFound from './Pages/NotFound';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import PrintAll from './Pages/PrintAll';
import PrintDrinks from './Pages/PrintDrinks';
import PrintSpirits from './Pages/PrintSpirits';
import PrintSmokes from './Pages/PrintSmokes';
import PrintDishes from './Pages/PrintDishes';
import url from "./Services/url.socket";
import { io } from "socket.io-client";
import VouchersPrintWorks from './Pages/VouchersPrintWorks';
import SessionsPrintWorks from './Pages/SessionsPrintWorks';

class App extends React.Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    const socket = io(url, {
      transports: ["websocket"],
      reconnection: true,
      reconnectionDelay: 60000,
    });
    socket.on(
      "print-session",
      async (response) => {
        console.log('Hey');
        console.log(response);
        window.open(response, '_blank');
      }
    );
  }

  render() {
    return (
      <Router>
        <Switch>
          <Route exact path='/application' component={Login} />
          <Route exact path='/application/login' component={Login} />
          <Route exact path='/application/home' component={Home} />
          <Route exact path='/application/users' component={Users} />
          <Route exact path='/application/clients' component={Clients} />
          <Route exact path='/application/products' component={Products} />
          <Route exact path='/application/stocks' component={Stocks} />
          <Route exact path='/application/transactions/detail' component={TransactionDetail} />
          <Route exact path='/application/transactions/wholesale' component={TransactionWholeSale} />
          <Route exact path='/application/transactions/manager' component={TransactionManager} />
          <Route exact path='/application/sessions/get/:idSession' component={GetOneSession} />
          <Route exact path='/application/sessions/inProgress' component={SessionsInProgress} />
          <Route exact path='/application/sessions/debt' component={Debt} />
          <Route exact path='/application/sessions/report' component={SessionsReport} />
          <Route exact path='/application/sessions/report/period' component={SessionsReportPeriod} />
          <Route exact path='/application/guichet/transactions' component={GuichetTransactions} />
          <Route exact path='/application/guichet/categories' component={GuichetCategories} />
          <Route exact path='/application/print/all' component={PrintAll} />
          <Route exact path='/application/print/Boissons' component={PrintDrinks} />
          <Route exact path='/application/print/Spiritueux' component={PrintSpirits} />
          <Route exact path='/application/print/Cigarettes' component={PrintSmokes} />
          <Route exact path='/application/print/Plts' component={PrintDishes} />

          <Route exact path='/application/print-service/voucher' component={VouchersPrintWorks} />
          <Route exact path='/application/print-service/sessions' component={SessionsPrintWorks} />
          
          <Route component={NotFound} />
        </Switch>
      </Router>
    );
  }
}

export default App;