import React from "react";
import { Button, Container, ListGroup, Spinner } from "react-bootstrap";
import NavBar from "../Components/NavBar";
import {
  getOneSession,
  getItemOfOneSession,
  deleteItem,
  finishSession,
  finishAndPaySession,
  paySession,
  reductionToZero,
  deleteOneSession,
} from "../Services/sessions";
import Cookies from "js-cookie";
import _ from "lodash";
import { getOneClient } from "../Services/clients";
import moment from "moment";
import PopupAddSessionItem from "../Components/PopupAddSessionItem";
import PopupEditSessionItem from "../Components/PopupEditSessionItem";
import PopupAddAccompaniment from "../Components/PopupAddAccompaniment";
import PopupViewAccompaniment from "../Components/PopupViewAccompaniment";
import PopupAddReduction from "../Components/PopupAddReduction";
import PopupEditSession from "../Components/PopupEditSession";
import VerifyStatus from "../Components/VerifyStatus";
import PopupPaySession from "../Components/PopupPaySession";
import { io } from "socket.io-client";
import url from "../Services/url.socket";

class GetOneSession extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      sessionInfo: {},
      sessionItem: [],

      isLoading: true,
      addMode: false,
      editMode: false,
      editSessionMode: false,
      editSumMode: false,
      accompMode: false,
      accompViewMode: false,
      reductionMode: false,
      payMode: false,
      error: false,
    };

    this.idSession = this.props.match.params.idSession;
    document.title = "Le Consulat - Sessions";
  }

  async componentDidMount() {
    const socket = io(url, {
      transports: ["websocket"],
      reconnection: true,
      reconnectionDelay: 60000,
    });
    socket.on(
      "edit-session",
      async (response) => {
        this.setState({ isLoading: true, }, window.location.reload(false))
      }
    );
    await getOneSession(this.idSession, Cookies.get("token"))
      .then(async (data) => {
        if (data.find) {
          await getItemOfOneSession(this.idSession, Cookies.get("token"))
            .then((res2) => {
              if (res2.find) {
                this.setState({
                  isLoading: false,
                  error: false,
                  sessionInfo: data.result[0],
                  sessionItem: res2.result,
                });
              } else {
                this.setState({
                  isLoading: false,
                  error: true,
                  sessionInfo: [],
                  sessionItem: {},
                });
              }
            })
            .catch(() => {
              this.setState({
                isLoading: false,
                error: true,
                sessionInfo: [],
                sessionItem: {},
              });
            });
        } else {
          this.setState({
            isLoading: false,
            error: true,
            sessionInfo: [],
            sessionItem: {},
          });
        }
      })
      .catch(() => {
        this.setState({
          isLoading: false,
          error: true,
          sessionInfo: [],
          sessionItem: {},
        });
      });
  }

  _updateItem = (info) => {
    this.editInfo = info;
    this.setState({ editMode: true });
  };

  _addAccompaniment = (info) => {
    this.accompInfo = info;
    this.setState({ accompMode: true });
  };

  _viewAccompaniment = (info) => {
    this.accompViewInfo = info;
    this.setState({ accompViewMode: true });
  };

  _deleteItem = (info) => {
    console.log("====================================");
    console.log(info);
    console.log("====================================");
    if (window.confirm("tes vous sre de vouloir supprimer cet lment?")) {
      deleteItem(Cookies.get("token"), info.idSession, info.idSessionsItem)
        .then((res) => {
          console.log("====================================");
          console.log(res);
          console.log("====================================");
          if (res.delete) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove("token");
            Cookies.remove("idUser");

            window.location.href = "/application/";
          } else {
            alert(
              "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
            );
          }
        })
        .catch(() => {
          alert(
            "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
          );
        });
    }
  };

  _displayItem = () => {
    if (this.state.sessionItem.length < 1) {
      return <em>Aucun lment</em>;
    } else {
      let arrayElement = [];
      if (this.state.sessionInfo.wasOver === 1) {
        for (let index in this.state.sessionItem) {
          arrayElement.push(
            <ListGroup.Item style={{ fontSize: 13.5 }}>
              <h4 style={{ marginBottom: -1 }}>
                {this.state.sessionItem[index].nameOfProduct}
              </h4>
              Quantit: {this.state.sessionItem[index].quantity}
              <br />
              Prix: {this.state.sessionItem[index].price}$<br />
              Total:{" "}
              {this.state.sessionItem[index].quantity *
                this.state.sessionItem[index].price}
              $<br />
              <a
                href="#"
                onClick={() => {
                  this._addAccompaniment(this.state.sessionItem[index]);
                }}
              >
                Ajouter un accompagnement
              </a>
              <br />
              <a
                href="#"
                onClick={() => {
                  this._viewAccompaniment(this.state.sessionItem[index]);
                }}
              >
                Voir les accompagnements
              </a>
            </ListGroup.Item>
          );
        }
      } else {
        for (let index in this.state.sessionItem) {
          arrayElement.push(
            <ListGroup.Item style={{ fontSize: 13.5 }}>
              <h4 style={{ marginBottom: -1 }}>
                {this.state.sessionItem[index].nameOfProduct}{" "}
                <a
                  style={{ fontSize: 18 }}
                  href="#"
                  onClick={() => {
                    this._updateItem(this.state.sessionItem[index]);
                  }}
                >
                  Modifier
                </a>{" "}
                <a
                  style={{ fontSize: 18 }}
                  onClick={() => {
                    this._deleteItem(this.state.sessionItem[index]);
                  }}
                  href="#"
                >
                  Supprimer
                </a>
              </h4>
              Quantit: {this.state.sessionItem[index].quantity}
              <br />
              Prix: {this.state.sessionItem[index].price}$<br />
              Total:{" "}
              {this.state.sessionItem[index].quantity *
                this.state.sessionItem[index].price}
              $<br />
              <a
                href="#"
                onClick={() => {
                  this._addAccompaniment(this.state.sessionItem[index]);
                }}
              >
                Ajouter un accompagnement
              </a>
              <br />
              <a
                href="#"
                onClick={() => {
                  this._viewAccompaniment(this.state.sessionItem[index]);
                }}
              >
                Voir les accompagnements
              </a>
            </ListGroup.Item>
          );
        }
      }
      return arrayElement;
    }
  };

  _addItem = () => {
    this.setState({ addMode: true });
  };

  _onClose = () => {
    this.setState({
      addMode: false,
      editMode: false,
      accompMode: false,
      accompViewMode: false,
      reductionMode: false,
      editSessionMode: false,
      payMode: false,
      editSumMode: false,
    });
  };

  _finish = () => {
    finishSession(Cookies.get("token"), this.idSession)
      .then((res) => {
        if (res.update) {
          window.location.reload();
        } else if (res.invalidToken) {
          Cookies.remove("token");
          Cookies.remove("idUser");

          window.location.href = "/application/";
        } else {
          alert(
            "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
          );
        }
      })
      .catch(() => {
        alert(
          "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
        );
      });
  };

  _finishAndPay = () => {
    this.setState({ payMode: true });
    // finishAndPaySession(Cookies.get('token'), this.idSession)
    //   .then(res => {
    //     if (res.update) {
    //       window.location.reload();
    //     } else if (res.invalidToken) {
    //       Cookies.remove('token');
    //       Cookies.remove('idUser');

    //       window.location.href = '/application/';
    //     } else {
    //       alert('Une erreur a eu lieu lors de l\'opration, ressayez ou contactez nous');
    //     }
    //   })
    //   .catch(() => {
    //     alert('Une erreur a eu lieu lors de l\'opration, ressayez ou contactez nous');
    //   });
  };

  _pay = () => {
    this.setState({ payMode: true });
    // paySession(Cookies.get('token'), this.idSession)
    //   .then(res => {
    //     if (res.update) {
    //       window.location.reload();
    //     } else if (res.invalidToken) {
    //       Cookies.remove('token');
    //       Cookies.remove('idUser');

    //       window.location.href = '/application/';
    //     } else {
    //       alert('Une erreur a eu lieu lors de l\'opration, ressayez ou contactez nous');
    //     }
    //   })
    //   .catch(() => {
    //     alert('Une erreur a eu lieu lors de l\'opration, ressayez ou contactez nous');
    //   });
  };

  _addReduction = () => {
    this.setState({ reductionMode: true });
  };

  _reductionToZero = () => {
    reductionToZero(Cookies.get("token"), this.idSession)
      .then((res) => {
        if (res.update) {
          window.location.reload();
        } else if (res.invalidToken) {
          Cookies.remove("token");
          Cookies.remove("idUser");

          window.location.href = "/application/";
        } else {
          alert(
            "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
          );
        }
      })
      .catch(() => {
        alert(
          "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
        );
      });
  };

  _editSession = () => {
    this.setState({ editSessionMode: true });
  };

  _deleteSession = () => {
    deleteOneSession(Cookies.get("token"), this.idSession)
      .then((res) => {
        if (res.delete) {
          window.location.href = "/application/sessions/inProgress";
        } else if (res.invalidToken) {
          Cookies.remove("token");
          Cookies.remove("idUser");

          window.location.href = "/application/";
        } else {
          alert(
            "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
          );
        }
      })
      .catch(() => {
        alert(
          "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
        );
      });
  };

  _editSum = () => {
    this.setState({ editSumMode: true });
  };

  _displayButton = () => {
    if (
      this.state.sessionInfo.wasOver === 1 &&
      this.state.sessionInfo.beenPaid
    ) {
      return (
        <Button variant="info" onClick={this._editSum}>
          Modifier la somme paye
        </Button>
      );
    } else if (this.state.sessionInfo.wasOver === 1) {
      return (
        <Button variant="warning" onClick={this._pay}>
          Paye
        </Button>
      );
    } else {
      return (
        <>
          <Button variant="danger" onClick={this._finish}>
            Termine
          </Button>{" "}
          <Button variant="warning" onClick={this._finishAndPay}>
            Termine et paye
          </Button>{" "}
          <Button variant="secondary" onClick={this._editSession}>
            Modifier
          </Button>{" "}
          <Button variant="primary" onClick={this._addReduction}>
            {this.state.sessionInfo.reduction >= 0
              ? "Ajouter une rduction"
              : "Modifier la rduction"}
          </Button>{" "}
          {this.state.sessionInfo.reduction > 0 ? (
            <Button variant="info" onClick={this._reductionToZero}>
              Remettre la rduction  0
            </Button>
          ) : null}
        </>
      );
    }
  };

  _displayButton2 = () => {
    if (this.state.sessionInfo.wasOver === 1) {
      return null;
    } else {
      return (
        <Button variant="success" onClick={this._addItem}>
          <i class="fas fa-plus-circle"></i> Ajouter un lment
        </Button>
      );
    }
  };

  _displayIframe = () => {
    if (this.state.sessionInfo.wasOver === 1) {
      // return <object data={this.state.sessionInfo.invoiceUrl.replace('http', 'https')} type="application/pdf" width="50%" height="500px"><embed src={this.state.sessionInfo.invoiceUrl.replace('http', 'https')} type="application/pdf" /></object>
      return (
        <a
          href={this.state.sessionInfo.invoiceUrl.replace("http", "https")}
          target="_blank"
        >
          Voir la facture
        </a>
      );
    }
  };

  render() {
    if (this.state.isLoading) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            <Spinner animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          </Container>
        </>
      );
    } else if (this.state.error) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            <em>Une erreur a eu lieu, ressayez ou contactez-nous</em>
          </Container>
        </>
      );
    } else {
      const sessionInfo = this.state.sessionInfo;
      let now = moment.unix(parseInt(sessionInfo.timestamp));
      return (
        <>
          <NavBar />
          <Container>
            <h1>Session</h1>
            {this._displayIframe()}
            <br />
            {this._displayButton()}
            {Cookies.get("idUser") == "1" || Cookies.get("idUser") == "2" ? (
              <Button variant="danger" onClick={this._deleteSession}>
                Supprimer la session
              </Button>
            ) : null}
            <h2>Information sur la session</h2>
            <table>
              <tr>
                <td>
                  <b>Clients: </b>
                </td>
                <td>{sessionInfo.nameOfClient}</td>
              </tr>
              <tr>
                <td>
                  <b>Serveur/Serveuse: </b>
                </td>
                <td>{sessionInfo.nameOfServer}</td>
              </tr>
              <tr>
                <td>
                  <b>Utilisateur: </b>
                </td>
                <td>{sessionInfo.nameOfUser}</td>
              </tr>
              <tr>
                <td>
                  <b>Date: </b>
                </td>
                <td>{now.format("DD/MM/yyyy [] HH:mm")}</td>
              </tr>
              <tr>
                <td>
                  <b>Total sans rduction: </b>
                </td>
                <td>{sessionInfo.total}$</td>
              </tr>
              <tr>
                <td>
                  <b>Rduction: </b>
                </td>
                <td>{sessionInfo.reduction}$</td>
              </tr>
              <tr>
                <td>
                  <b>Total: </b>
                </td>
                <td>{sessionInfo.total - sessionInfo.reduction}$</td>
              </tr>
              <tr>
                <td>
                  <b>Pay: </b>
                </td>
                <td>{sessionInfo.amountPaid}$</td>
              </tr>
              <tr>
                <td>
                  <b>Reste  pay: </b>
                </td>
                <td>
                  {sessionInfo.total -
                    sessionInfo.reduction -
                    sessionInfo.amountPaid}
                  $
                </td>
              </tr>
            </table>
            <br />
            <h2>lement de la session</h2>
            <ListGroup style={{ width: "25rem" }}>
              {this._displayButton2()}
              {this._displayItem()}
            </ListGroup>
            {this.state.addMode ? (
              <PopupAddSessionItem
                idSession={this.idSession}
                callback={this._onClose}
              />
            ) : null}
            {this.state.editMode ? (
              <PopupEditSessionItem
                data={this.editInfo}
                callback={this._onClose}
              />
            ) : null}
            {this.state.accompMode ? (
              <PopupAddAccompaniment
                data={this.accompInfo}
                callback={this._onClose}
              />
            ) : null}
            {this.state.accompViewMode ? (
              <PopupViewAccompaniment
                data={this.accompViewInfo}
                callback={this._onClose}
              />
            ) : null}
            {this.state.reductionMode ? (
              <PopupAddReduction
                idSession={this.state.sessionInfo.idSession}
                default={this.state.sessionInfo.reduction}
                callback={this._onClose}
              />
            ) : null}
            {this.state.editSessionMode ? (
              <PopupEditSession
                data={this.state.sessionInfo}
                callback={this._onClose}
              />
            ) : null}
            {this.state.payMode ? (
              <PopupPaySession
                changeData={false}
                data={this.state.sessionInfo}
                callback={this._onClose}
              />
            ) : null}
            {this.state.editSumMode ? (
              <PopupPaySession
                changeData={true}
                data={this.state.sessionInfo}
                callback={this._onClose}
              />
            ) : null}
            <VerifyStatus />
          </Container>
        </>
      );
    }
  }
}

export default GetOneSession;
