import React from "react";
import { Button, Modal, Form, Alert } from "react-bootstrap";
import Cookies from "js-cookie";
import { paySession, finishAndPaySession, editMoneyOfSession } from "../Services/sessions";
import MethodsList from "./MethodsList";

class PopupPaySession extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: true,

      pm: this.props.changeData ? this.props.data.idMethod : null,
      amountPaid: this.props.changeData
        ? this.props.data.amountPaid
        : this.props.data.total - this.props.data.reduction,

      isLoading: false,
      invalidForm: false,
      error: false,
    };
  }

  _handleClose = () => {
    if (this.props.callback) {
      this.props.callback();
    } else {
      this.setState({ show: false });
    }
  };

  _handlePM = (id, method) => {
    this.setState({ pm: id });
  };

  _handleAmountPaid = (event) => {
    this.setState({ amountPaid: event.target.value });
  };

  _onClick = (event) => {
    event.preventDefault();

    this.setState({ isLoading: true });

    const toSend = {
      paymentMethod: Number(this.state.pm),
      amountPaid: Number(this.state.amountPaid),
    };

    if (this.props.changeData) {
      editMoneyOfSession( toSend, Cookies.get("token"), this.props.data.idSession)
        .then((res) => {
          if (res.update) {
            window.location.reload();
          } else if (res.invalidToken) {
            Cookies.remove("token");
            Cookies.remove("idUser");

            window.location.href = "/application/";
          } else {
            alert(
              "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
            );
          }
        })
        .catch(() => {
          alert(
            "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
          );
        });
    } else {
      if (!this.props.data.wasOver) {
        finishAndPaySession(
          Cookies.get("token"),
          this.props.data.idSession,
          toSend
        )
          .then((res) => {
            if (res.update) {
              window.location.reload();
            } else if (res.invalidToken) {
              Cookies.remove("token");
              Cookies.remove("idUser");

              window.location.href = "/application/";
            } else {
              alert(
                "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
              );
            }
          })
          .catch(() => {
            alert(
              "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
            );
          });
      } else {
        paySession(Cookies.get("token"), this.props.data.idSession, toSend)
          .then((res) => {
            if (res.update) {
              window.location.reload();
            } else if (res.invalidToken) {
              Cookies.remove("token");
              Cookies.remove("idUser");

              window.location.href = "/application/";
            } else {
              alert(
                "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
              );
            }
          })
          .catch(() => {
            alert(
              "Une erreur a eu lieu lors de l'opration, ressayez ou contactez nous"
            );
          });
      }
    }
  };

  _displayAlert = () => {
    if (this.state.invalidForm) {
      return <Alert variant="warning">Des champs ne sont pas valides</Alert>;
    } else if (this.state.error) {
      return (
        <Alert variant="danger">
          Une erreur a eu lieu, ressayer ou contactez-nous
        </Alert>
      );
    }
  };

  _displayButton = () => {
    if (
      this.state.pm !== null &&
      this.state.amountPaid <= this.props.data.total - this.props.data.reduction
    ) {
      return (
        <Button variant="success" onClick={this._onClick}>
          Confimer
        </Button>
      );
    } else {
      return (
        <Button variant="success" disabled>
          Confirmer
        </Button>
      );
    }
  };

  render() {
    return (
      <Modal
        show={this.state.show}
        onHide={this._handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Payer une session</Modal.Title>
        </Modal.Header>
        <Modal.Body style={{ fontSize: 15 }}>
          <Form.Label>
            Montant pay (en $) (dans le cas d'un crdit, entrer le montant du
            crdit dj payer):{" "}
          </Form.Label>
          <Form.Control
            onChange={this._handleAmountPaid}
            value={this.state.amountPaid}
            type="number"
            placeholder="Le montant pay par le client..."
            aria-describedby="btnSubmit"
          />
          <br />
          <MethodsList onChange={this._handlePM} />
          {this._displayAlert()}
        </Modal.Body>
        <Modal.Footer>
          {this._displayButton()}
          <Button variant="danger" onClick={this._handleClose}>
            Fermer
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
}

export default PopupPaySession;
