import React from 'react';
import { Container, Button, Spinner } from 'react-bootstrap';
import NavBar from '../Components/NavBar';
import SessionsInProgressList from '../Components/SessionsInProgressList';
import PopupAddSession from '../Components/PopupAddSession';
import url from "../Services/url.socket";
import { io } from "socket.io-client";

class SessionsInProgress extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      addMode: false,
      isLoading: false,
    };
    document.title = "Le Consulat - Sessions";
  }

  componentDidMount() {
    const socket = io(url, {
      reconnection: true,
      reconnectionDelay: 60000,
    });
    socket.on(
      "new-session",
      async (response) => {
        console.log('Hey');
        this.setState({ isLoading: true, }, window.location.reload(false))
      }
    );
    socket.on(
      "edit-session",
      async (response) => {
        this.setState({ isLoading: true, }, window.location.reload(false))
      }
    );
  }

  _addSession = () => {
    this.setState({ addMode: true });
  };

  _onClose = () => {
    this.setState({ addMode: false });
  }
  render() {
    if (this.state.isLoading) {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Sessions en cours</h1>
            <Spinner animation="border" role="status">
              <span className="sr-only">Loading...</span>
            </Spinner>
          </Container>
        </>
      );
    } else {
      return (
        <>
          <NavBar />
          <Container>
            <h1>Sessions en cours</h1>
            <Button variant='success' onClick={this._addSession}><i class="fas fa-plus-circle"></i> Commencer une nouvelle session</Button>
            <br /><br />
            <SessionsInProgressList />
          </Container>
          {this.state.addMode ? <PopupAddSession callback={this._onClose} /> : null}
        </>
      );
    }
  }
}

export default SessionsInProgress;